import { VclFacebook } from "vue-content-loading";

export default {
  layout: "notKeepAlive",
  components: { VclFacebook },
  data() {
    return {
      searched: [],
      next: "",
      dGeneral: "/",
      dQandA: "/q/",
      dBlog: "/b/",
      nodata: false,
      showPag: false
    };
  },
  async fetch() {
    var self = this;
    await self.$axios
      .$get(process.env.baseUrl + "/serach/" + self.$route.params.key)
      .then(function(posts) {
        self.searched = posts.results;
        if (posts.next != null) {
          self.next = posts.next;
          self.showPag = true;
        }
      })
      .catch(function(e) {
        console.log(e);
        self.nodata = true;
      });
  },
  computed: {},
  methods: {
    async loadData() {
      if (this.next != null) {
        var self = this;
        await self.$axios
          .$get(self.next)
          .then(function(posts) {
            posts.results.forEach(element => {
              self.searched.push(element);
            });
            self.next = posts.next;
          })
          .catch(function(error) {
            console.log("No Net" + error);
          })
          .finally(function() {});
      } else {
        // alert("no more data");
      }
    },

    async setView(viwes, url, slug) {
      // await this.$axios
      //   .$put(process.env.baseUrl + `/${url}${slug}`, {
      //     view: viwes + 1
      //   })
      //   .then(function(response) {})
      //   .catch(function(e) {
      //     console.log("setview " + e);
      //   });
    }
  }
};
